'use client'

import AddCategory from "@/components/category/AddCategory"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Separator } from "@/components/ui/separator"
import { CategoryModel } from "@/type/category.model"
import { useEffect, useState } from "react"
import { categoryService } from "@/lib/services/categoryService"

export default function Page(){

    const [search, setSearch] = useState<string>("")
    const [open, setOpen] = useState<boolean>(false)
    const [categoriesData, setCategoriesData] = useState<CategoryModel[]>([])
    const [actionType, setActionType] = useState<string>('add')
    const [categoryDetail, setCategoryDetail] = useState<CategoryModel>()
    const [page, getPage] = useState<number>(1)
    const [totalPage, setTotalPage] = useState<number>(0)
    
    const getCategories =()=>{
        
       categoryService.getCategories({page,search}).then((res)=>{
          setCategoriesData(res.data.data)
          setTotalPage(res.data.data.count)
       }).catch((err)=>{
        console.log(err)
       })

    }

    useEffect(()=>{
     getCategories()
    },[])

    useEffect(()=>{
       if(!open){
            setActionType('add')
           setCategoryDetail({
             id:'',
             name:'',
             description:'',
             created_date:'',
             updated_date:''
           })
       }
    },[open])

    return(
         <div className="w-full">
            <div className="flex justify-between items-center px-12 py-4">
              <input className="max-w-md bg-white px-4 py-3 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500" 
              placeholder="search"
              onChange={(e)=>setSearch(e.target.value)}
              />
              
              <Dialog open={open} onOpenChange={setOpen}>
  <DialogTrigger className="bg-black hover:bg-gray-500 cursor-pointer text-white px-4 py-2 rounded-lg shadow-lg" >Add Category</DialogTrigger>
  <DialogContent>
    <DialogHeader>
      <DialogTitle>Add Category</DialogTitle>
    </DialogHeader>
    <Separator />
     
     <AddCategory  setOpen={setOpen} actionType={actionType} categoryDetail={categoryDetail} getCategories={getCategories}/>
    
  </DialogContent>
</Dialog>
            </div>
            <div className="flex flex-col justify-center">

            </div>
         </div>
    )
}